import React from 'react';

const complete = (props) => (
    <div className='booking-complete booking-complete-hidden'>
        <div className='booking-complete-exit' onClick={() => closeComplete()}></div>
        <div className='booking-complete-message'></div>
    </div>
);

export default complete;

const closeComplete = () => {
    document.querySelector('.booking-complete').classList.remove('booking-complete-visible');
    document.querySelector('.booking-complete').classList.add('booking-complete-hidden');
};