import React from 'react';
import timesMenu from  '../../../../utils/reserve/dropdown/GetTimes';
import personsMenu from '../../../../utils/reserve/dropdown/PersonAmount';
import SimpleBar from 'simplebar-react';
import 'simplebar/dist/simplebar.min.css';

const dropdown = (props) => (
    <div className={getClassName(props.showDropdown, 'reservation-entry-dropdown')}>
        <SimpleBar>
        {
            (props.text === 'Time' ? timesMenu : personsMenu).map((time) => (
                <div className='reservation-entry-dropdown-item' onClick={props.itemClick}>
                    {time}
                </div>
            ))
        }
    </SimpleBar>
    </div>
);

const getClassName = (visible, className) => (visible ? `${className} ${className}-visible` : `${className} ${className}-hidden`)

export default dropdown;