import React, { Component } from 'react';
import './App.css';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import Navigation from './components/navigation/main/js/Navigation';
import Home from './pages/home/components/home/main/js/Home';
import Footer from './components/footer/main/js/Footer';
import Menu from './pages/menu/components/main/js/Menu';
import NotFound from './components/not-found/NotFound';

class App extends Component {
  render() {
    return (
      <Router>
          <div className='App'>
            <Navigation />
            <Switch>
              <Route path='/not-found' component={NotFound} />
              <Route path='/menu/:type' component={Menu} />
              <Route exact path='/' component={Home} />
            </Switch>
            <Footer />
          </div>
      </Router>
    );
  }
}

export default App;
