import React from 'react';
import CategoryLine from '../line/CategoryLine';
import categoryLines from '../../../../utils/category/category-lines/AllCategories';

const category = (props) => (
    <div className='category'>
        {
            categoryLines.map((line, index) => (
                <CategoryLine number={index + 1} line={line} />
            ))
        }
        {/* <div className='home-block home-block1'>
            <div className='home-block-inner1'></div>
            <div className='home-block-inner2'></div>
        </div>
        <div className='home-block home-block2'>
            <div className='home-block-inner1'></div>
            <div className='home-block-inner2'></div>
        </div> */}
    </div>
);

export default category;