import React, { Component } from 'react';
import '../css/Menu.css'
import Api from '../../../../../api/menu.json'
import CategoryMenu from '../../category-menu/CategoryMenu';
import { Redirect } from 'react-router-dom';

export default class Menu extends Component {
    componentDidMount() {

    }
    render() {
        let url = this.props.match.url;
        console.log(url);
        if(url === '/menu/all') {
            return(
                <div className='menu'>
                    <CategoryMenu menu={Api.categorys[0]['menu-items']} category={'starter'}/>
                    <CategoryMenu menu={[...Api.categorys[2]['menu-items'], ...Api.categorys[5]['menu-items']]} category={'main'}/>
                </div>
            );
        }
        else if(url === '/menu/starter') {
                return(
                    <div className='menu'>
                        <CategoryMenu menu={Api.categorys[0]['menu-items']} category={'starter'}/>
                    </div>
                );
        }
        else if(url === '/menu/main') {
            return(
                <div className='menu'>
                    <CategoryMenu menu={[...Api.categorys[2]['menu-items'], ...Api.categorys[5]['menu-items']]} category={'main'}/>
                </div>
            );
        }
        else {
            return <Redirect to={'/not-found'} />
        }
    }
}