import React from 'react';

const categoryMenu = (props) => (
    <div className={'menu-' + props.category}>
        {
            props.menu.map((item) => (
                <div className='menu-item'>
                    <div className='menu-item-title'>
                        {item.name}
                    </div>
                    <div className='menu-item-description'>
                        {item.description}
                    </div>
                    <div className='menu-item-price'>
                        Price: Â£{Math.floor(item['sub-items'][0].price/10)}
                    </div>
                    <div className='menu-item-cuisine'>
                        Cuisine: {item['sub-items'][0].cuisine_name}
                    </div>
                </div>
            ))
        }
    </div>
);

export default categoryMenu;