import React from 'react';
import { Link } from 'react-router-dom';

const line = (props) => {
    return(
        <div className={`category-line category-line${props.number}`}>
            <div className={`category-line-inner${(props.number * 2) - 1}`}>
                <Link to={props.line[0].link} className='category-button'>{props.line[0].name}</Link>
            </div>
            <div className={`category-line-inner${props.number * 2}`}>
                <Link to={props.line[1].link} className='category-button'>{props.line[1].name}</Link>
            </div>
        </div>
    );
};

export default line;