import React from 'react';
import Dropdown from '../dropdown/Dropdown';
import Calendar from '../calendar/Calendar'

const entry = (props) => {
    if(props.type === 'calendar') {
        return (
            <div className='reservation-entry'>
                {textArea(props.value, props.mainClick, props.text, props.showDropdown)}
                <Calendar itemClick={props.itemClick} showDropdown={props.showDropdown} />
            </div>
        );
    }
    else {
        return(
            <div className='reservation-entry'>
                {textArea(props.value, props.mainClick, props.text, props.showDropdown)}
                <Dropdown itemClick={props.itemClick} text={props.text} showDropdown={props.showDropdown} />
            </div>
        );
    }
}

export default entry;

const arrow = (visible) => (
    <div className='arrow-wrapper'>
        <div className={visible ? 'arrow arrow-up' : 'arrow arrow-down'}>
        </div>
    </div>
)

const textArea = (text, click, type, visible) => (
    <div className='reservation-entry-text-arrow' onClick={(name) => click(type.toLowerCase())}>
        <div className='reservation-entry-text'>{text}</div>
        {arrow(visible)}
    </div>
);