import React from 'react';
import SectionList from '../section-list/SectionList';
import SectionLists from './SectionLists';

const section = (props) => (
    <div className='footer-section'>
        <SectionList type={'Food Menu'} list={SectionLists['foodMenu']} />
        <SectionList type={'Contact'} list={SectionLists['contact']} />
        <SectionList type={'Address'} list={SectionLists['address']} />
    </div>
);

export default section;