import React from 'react';
import { Link } from 'react-router-dom';

const sectionList = (props) => (
    <div className='footer-section-list'>
        <div className='footer-section-title'>
            {props.type}
        </div>
        <div className='footer-section-list-items'>
            <div className={'footer-section-list-items-' + convertToKebab(props.type)}>
                {
                    props.list.map((item) => {
                        if(props.type === 'Address') {
                            return <div className='footer-section-list-item'>{item}</div>
                        }
                        else {
                            return <Link className='footer-section-link footer-section-list-item' to={item.link}>{item.name}</Link>
                        }
                    })
                }
            </div>
        </div>
    </div>
);

export default sectionList;

const convertToKebab = (string) => (
    string
        .split(' ')
        .map((word) => word[0].toLowerCase() + word.substring(1))
        .join('-')
)