const handleBookClick = (e) => {
    let entries = Array.from(document.querySelectorAll('.reservation-entry'));
    let allSelected = entries.every((entry) => {
        return !['Date', 'Time', 'Persons']
            .includes(entry.firstElementChild.firstChild.textContent)
    });
    if(allSelected) {
        let date = getEntryVal(0);
        let time = getEntryVal(1);
        let persons = getEntryVal(2);
        let bookCompleteMessage = document.querySelector('.booking-complete-message');
        let message = `Booked at ${date}, ${time} for ${persons} `;
        message += persons === '1' ? 'person' : 'people';
        bookCompleteMessage.textContent = message;
        let bookingComplete = document.querySelector('.booking-complete');
        bookingComplete.classList.remove('booking-complete-hidden');
        bookingComplete.classList.add('booking-complete-visible');
    }
    else {
        alert('Please select all options');
    }
};

const getEntryVal = (index) => {
    let time = document.querySelector('.reservation-entries').children[index];
    let text = time.firstElementChild.firstElementChild.textContent;
    return text;
}

export default handleBookClick;