import React, { Component } from 'react';
import '../css/Navigation.css';
import { Link } from 'react-router-dom';
import contactClick from '../../../../utils/navigation/ContactClickEvent';

export default class Navigation extends Component {

    componentDidMount() {
        let navigation = document.querySelector('.navigation');
        window.addEventListener('scroll', (e) => {
            let yOffset = window.pageYOffset;
            if(yOffset < 400) {
                navigation.classList.remove('navigation-hidden');
                navigation.classList.remove('navigation-little-visible');
            }
            else if(yOffset < 700) {
                navigation.classList.remove('navigation-hidden');
                navigation.classList.add('navigation-little-visible');
            }
            else {
                navigation.classList.remove('navigation-little-visible');
                navigation.classList.add('navigation-hidden');
            }
        });
    }

    render() {
        return(
            <div className='navigation navigation-visible'>
                <div className='navigation-empty-first'></div>
                <Link to={'/'} className='navigation-item navigation-home'><div className='navigation-item-text'>Home</div></Link>
                <Link to={'/'} className='navigation-item navigation-food-menu' onClick={() => console.log('boo')}>
                <div className='navigation-item-text'>Food Menu</div>
                    <div className='navigation-food-menu-list'>
                        <div className='navigation-food-menu-list-shape'>
                            <div className='navigation-categories-list'>
                                <Link to={'/menu/all'}>Full Menu</Link>
                                <Link to={'/menu/starter'}>Starter</Link>
                                <Link to={'/menu/main'}>Main</Link>
                                <Link to={'/menu/main'}>Dessert</Link>
                            </div>
                        </div>
                    </div>
                </Link>
                <div className='navigation-item navigation-contact'>
                    <div className='navigation-item-text' onClick={() => contactClick()}>Contact</div>
                </div>
            </div>
        );
    }
}