import React from 'react';
import Calendar from 'react-calendar';

const calendar = (props) => (
    <div className={getClassName(props.showDropdown, 'reservation-entry-dropdown') + ' reservation-entry-dropdown-calendar'} >
        <Calendar 
            onChange={(date) => props.itemClick(null, 'date', getFormattedDate(date))}
        />
    </div>
);

const getClassName = (visible, className) => (visible ? `${className} ${className}-visible` : `${className} ${className}-hidden`)

const getFormattedDate = (date) => `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;

export default calendar;