import React from 'react';
import '../css/Reserve.css';
import Entry from '../../entry/Entry';
import handleBookClick from '../../../../../utils/reserve/dropdown/HandleBookClick';
import BookingComplete from './BookComplete'; 


export default class Reservation extends React.Component {
    constructor() {
        super();
        this.state = {
            visibleDropdown: {
                date: false,
                time: false,
                persons: false
            },
            dropdownValues: {
                date: 'Date',
                time: 'Time',
                persons: 'Persons'
            }
        }
    }
    handleDropdownItemClick(e, name, date) {
        let newDropdownValues = Object.keys(this.state.dropdownValues)
            .reduce((dropdownValues, key) => {
                if(key === name) {
                    dropdownValues[key] = key === 'date' ? date : e.target.textContent;
                }
                else {
                    dropdownValues[key] = this.state.dropdownValues[key];
                }
                return dropdownValues;
            }, {});
        let newVisibleDropdown = Object.keys(this.state.visibleDropdown)
            .reduce((visibleDropdown, key) => {
                visibleDropdown[key] = false;
                return visibleDropdown;
            }, {});
        this.setState({dropdownValues: newDropdownValues, visibleDropdown: newVisibleDropdown});
    }

    handleDropdownMainButton(name) {
        let newVisibleDropdown = Object.keys(this.state.visibleDropdown)
            .reduce((visibleDropdown, key) => {
                visibleDropdown[key] = key === name && this.state.visibleDropdown[name] === false;
                return visibleDropdown;
            }, {});
        this.setState({visibleDropdown: newVisibleDropdown});
    }

    handleClick(e) {
        let target = e.target;
        if(target.className === 'reservation' || target.className === 'reservation-entries') {
            let newVisibleDropdown = Object.keys(this.state.visibleDropdown)
                .reduce((visibleDropdown, key) => {
                    visibleDropdown[key] = false;
                    return visibleDropdown;
                }, {});
            this.setState({ visibleDropdown: newVisibleDropdown });
        }
    }

    render() {
        return(
            <div className='reservation' onClick={(e) => this.handleClick(e)}>
                <div className='reservation-title'><h4>Reservations</h4></div>
                <div className='reservation-entries-book-button'>
                    <div className='reservation-entries'>
                        <Entry type={'calendar'} mainClick={(name) => this.handleDropdownMainButton(name)} text={'Date'} showDropdown={this.state.visibleDropdown.date} value={this.state.dropdownValues.date} itemClick={(e, name, date) => this.handleDropdownItemClick(e, name, date)} />
                        <Entry type={'dropdown'} mainClick={(name) => this.handleDropdownMainButton(name)} text={'Time'} showDropdown={this.state.visibleDropdown.time} value={this.state.dropdownValues.time} itemClick={(e) => this.handleDropdownItemClick(e, 'time')} />
                        <Entry type={'dropdown'} mainClick={(name) => this.handleDropdownMainButton(name)} text={'Persons'} showDropdown={this.state.visibleDropdown.persons} value={this.state.dropdownValues.persons} itemClick={(e) => this.handleDropdownItemClick(e, 'persons')}  />
                    </div>
                    {reservationButton}
                </div>
                <BookingComplete />
            </div>
        );
    }
}

const reservationButton = (
    <div className='reservation-book-button' onClick={(e) => handleBookClick(e)}>
        <div className='reservation-book-button-text'>Reserve Seats</div>
        <span className='booking-button-lines'>
            <span className='booking-button-line'></span>
            <span className='booking-button-line'></span>
            <span className='booking-button-line'></span>
            <span className='booking-button-line'></span>
        </span>
    </div>
);