import React from 'react';
import MeatCook from '../../../../../video/meat_cook1.mp4';

const video = (props) => (
    <div className='home-video'>
         <video autoPlay={true} loop={true}>
            {/** video from - https://videos.pexels.com/videos/cooking-meat-857779 **/}
            <source src={MeatCook} type='video/mp4'></source>
        </video>
        <div className='home-video-buttons'>
        <div className='home-video-buttons-category' onClick={() => scrollTo(860)}>View Categories</div>
        <div className='home-video-buttons-reserve' onClick={() => scrollTo(1854)}>Reserve a Seat</div>
        </div>
    </div>
);

const scrollTo = (y) => window.scroll(0, y);

export default video;