const getTimes  = () => (
    Array.apply(null, {length: 22}).map((val, index) => index).slice(9)
        .map((hour) => {
            let formattedtHour = hour < 10 ? `0${hour}` : `${hour}`;
            return getHour(formattedtHour);
        })
        .reduce((hours, hour) => {
            return [...hours, ...hour]; 
        }, [])
);

const getHour = (formattedHour) => (
    Array.apply(null, {length: 6}).map((val, index) => index)
        .map((mins) => `${formattedHour}:${mins}0`)
);

export default getTimes();